import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { BASE_URL, FILES_URL } from "../../constants/index";

const User = () => {
  const [sell_point_id, setsell_point_id] = useState( "608b599dce9aa300229f9b87");

  const [user, setUser] = useState({
    name: "",
    username: "",
    email: "",
    phone: "",
    webiste: ""
  });
  const { id } = useParams();
  console.log(id);
  useEffect(() => {
    loadUser();
  }, []);
  const loadUser = async () => {
    console.log(id);
    const data = {
      pId : id,
      sell_point_id : sell_point_id
    }
    const res = await axios.post(`${BASE_URL}/getProductInfo`,data);
    setUser(res.data);
    console.log(res.data);
  };
  return (
    <div className="container py-4">
      <Link className="btn btn-primary" to="/">
        back to Home
      </Link>
      <h1 className="display-4">إسم المنتج: {user.name}</h1>
      <hr />
      <ul className="list-group w-50">
        <li className="list-group-item">إسم المنتج: {user.name}</li>
      
        <li className="list-group-item">السعر: {user.price}</li>
        <td ><img style={{width:250 , height:250}} src={`${FILES_URL}${user.photo}`}/></td>
      </ul>
    </div>
  );
};

export default User;
