import React  ,{useState} from "react";
import { Link, NavLink } from "react-router-dom";
import 'antd/dist/antd.css'; // or 'antd/dist/antd.less'
import {BASE_URL} from '../../constants/index'; 
import axios from "axios";

import {Switch} from 'antd'
const Navbar = () => {
  const [toggle, setToggle] = useState(true);
  const [status, setstatus] = useState();
  const [sell_point_id, setsell_point_id] = useState( "608b599dce9aa300229f9b87");

  const toggler = ()=>{
    toggle ? setToggle(false):setToggle(true);
    console.log(toggle);
    setstatus(toggle)
    reverseStatus(toggle ,sell_point_id)
  }

  const reverseStatus = async (toggle , sell_point_id) => {
    const res = await axios.post(`${BASE_URL}/chageState`,{
      status:toggle,
      spId:sell_point_id
    });
    
  };


  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
      <div className="container">
        <Link className="navbar-brand"  to="/">
          نقطة بيع جيتك
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <NavLink className="nav-link" exact to="/">
                الرئيسية
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" exact to="/about">
                الصفحة الشخصية
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" exact to="/contact">
                التواصل مع الادارة
              </NavLink>
            </li>
            <li className="nav-item">
              <div  className="nav-link">
              <Switch onChange={toggler}/>
              {toggle?<span>offline</span>:<span>online</span>}
              </div>
            </li>
          </ul>
        </div>

        <Link className="btn btn-outline-light" to="/users/add">إضافة منتج </Link>
      </div>
    </nav>
  );
};

export default Navbar;
