import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import {BASE_URL} from '../../constants/index'; 
const AddUser = () => {
  let history = useHistory();

  const [name, setname] = useState('');
  const [price, setprice] = useState();
  const [sell_point_id, setsell_point_id] = useState( "608b599dce9aa300229f9b87");
  const [pImage, setpImage] = useState();


  const onFileChange = (e) => {
    const pImage = e.target.files[0];
    setpImage(pImage);
  };
  const onSubmit = async (e) => {
    
    const data = new FormData();
    data.append("name",name);
    data.append("price",price);
    data.append("sell_point_id",sell_point_id);
    data.append("pImage",pImage);

    e.preventDefault();
    await axios.post(`${BASE_URL}/createProduct`, data, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    history.push("/");
  };
  return (
    <div className="container">
      <div className="w-75 mx-auto shadow p-5">
        <h2 className="text-center mb-4">إضافة منتج</h2>
        <form onSubmit={(e) => onSubmit(e)} encType="multipart/form-data" method="post">
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="إسم المنتج"
              name="name"
              value={name}
              onChange={(e) => setname(e.target.value)}
            />
          </div>
         
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="سعر المنتج"
              name="price"
              value={price}
              onChange={(e) => setprice(e.target.value)}
            />
          </div>
   
          <div className="form-group">
            <label>
              صورة للمنتج
            </label>
            <input
              type="file"
              className="form-control form-control-lg"
              placeholder="أختر صورة للمنتج"
              name="pImage"
              onChange={(e) => onFileChange(e)}
            />
          </div>
          <button className="btn btn-primary btn-block">حفظ المنتج</button>
        </form>
      </div>
    </div>
  );
};

export default AddUser;
